.section {
  width: 500px;
  border: 2px solid black;
  margin: 10px 30px;
}

.title {
  text-align: center;
  text-transform: capitalize;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 30px;
  font-weight: 700;
}

.list {
  list-style: none;
  display: flex;
  align-items: center;
}
.listItem {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 25px;
  background-color: lightgreen;
  border-radius: 10%;
  margin: 10px 0 10px 10px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 20px;
  font-weight: bold;
  width: 100px;
}
